import React, { useContext, useEffect, useState } from 'react'
import './ProductItem.module.css'
import { Link } from 'react-router-dom'
import { CartContext } from '../../Context/CartContext'
import toast from 'react-hot-toast';
import { WishlistContext } from '../../Context/WishlistContext';

export default function ProductItem({ product }) {
  const { addProductToCart, setNumOfCartItems } = useContext(CartContext);
  const { addProductToWishlist, removeProductWishlist, setNumOfWishlistItems } = useContext(WishlistContext);
  const [heart, setHeart] = useState(false);

  async function addProduct(id) {
    let { data } = await addProductToCart(id);
    if (data.status === 'success') {
      toast.success(data.message, {
        position: 'bottom-right',
        duration: 4000,
      });
      setNumOfCartItems(data.numOfCartItems)
    }
  }

  async function addWishlist(id) {
    let { data } = await addProductToWishlist(id);
    if (data.status === 'success') {
      toast.success(data.message, {
        position: 'bottom-right',
        duration: 4000,
      });
      setNumOfWishlistItems(data.count); 
    }
  }
  
  async function removeWishlist(id) {
    let { data } = await removeProductWishlist(id);
    if (data.status === 'success') {
      toast.success(data.message, {
        position: 'bottom-right',
        duration: 4000,
      });
      setNumOfWishlistItems(data.count); 
    }
  }

  useEffect(() => {
    const isWishlistItem = localStorage.getItem(`wishlist_${product.id}`);
    if (isWishlistItem === 'true') {
      setHeart(true);
    }
  }, []);


  return (
    <div className='col-md-2 product position-relative'>
      <Link to={`details/${product.id}`}>
        <img src={product.imageCover} className='w-100' alt="product" />
        <h6 className='text-main '>{product.category.name}</h6>
        <h2 className='h5'>{product.title.split(' ').slice(0, 2).join(' ')}</h2>
        <div className='d-flex justify-content-between my-3'>
          <span>{product.price} EPG</span>
          <span><i className='fa-solid fa-star rating-color'></i> {product.ratingsAverage}</span>
        </div>
      </Link>
      <i className={`fa-solid fa-heart fa-2x position-absolute top-0 end-0 m-3 cursor-pointer ${heart ? 'text-danger' : 'text-secondary'}`}
        onClick={async () => {
          if (heart) {
            await removeWishlist(product.id);
            localStorage.removeItem(`wishlist_${product.id}`);
          } else {
            await addWishlist(product.id);
            localStorage.setItem(`wishlist_${product.id}`, 'true');
          }
          setHeart(!heart);
        }}>
      </i>
      <button className='btn bg-main w-100 mb-2 text-white' onClick={() => { addProduct(product.id) }}>ADD TO CART</button>
    </div>
  )
}

